import { FileText, MoreVertical, Tool, Trash } from "react-feather"
import { useDispatch } from "react-redux"
import DropdownItem from "reactstrap/lib/DropdownItem"
import DropdownMenu from "reactstrap/lib/DropdownMenu"
import DropdownToggle from "reactstrap/lib/DropdownToggle"
import UncontrolledDropdown from "reactstrap/lib/UncontrolledDropdown"
import { startDeleteRegister } from "@redux/actions/custom"
import { Link } from "react-router-dom"
import Badge from "reactstrap/lib/Badge"

import { useContext } from "react"
import { AbilityContext } from '@src/utility/context/Can'

export const vehiclesList = [
  {
    name: 'Nº',
    selector: 'id',
    sortable: true,
    searchable: true,
    minWidth: '50px',
    width: '8%'
  },
  {
    name: 'Matrícula',
    selector: 'plate',
    sortable: true,
    searchable: true,
    width: '18%'
  },
  {
    name: 'Conductor',
    selector: 'idCarrier',
    sortable: true,
    searchable: true,
    width: '20%'
  },
  {
    name: 'Número de bastidor',
    selector: 'frameNumber',
    sortable: true,
    searchable: true,
    width: '20%'
  },
  {
    name: 'Fecha de ITV',
    selector: 'ITVdate',
    sortable: true,
    searchable: true,
    width: '20%'
  },
  {
    name: 'Estado',
    sortable: true,
    searchable: true,
    width: '10%',
    cell: row => {
      return (
        <>

          {row.idStatus === 2 ?
            (<Badge color='light-success'>
              Activo
            </Badge>)
            :
            (<Badge color='light-danger'>
              Inactivo
            </Badge>)
          }
        </>
      )
    }
  },
  {
    name: '',
    width: '5%',
    cell: (row, index) => {

      const dispatch = useDispatch()
      const ability = useContext(AbilityContext)

      return (
        <div className='d-flex'>
          <UncontrolledDropdown>
            <DropdownToggle className='pr-1' tag='span'>
              <MoreVertical size={15} />
            </DropdownToggle>
            <DropdownMenu right>
              {ability.can('update', 'vehicles') && (
                <Link to={`./vehicles/edit/${row.id}`}>
                  <DropdownItem tag='a' href='/' className='w-100'>
                    <FileText size={15} />
                    <span className='align-middle ml-50'>Detalles</span>
                  </DropdownItem>
                </Link>
              )}
              {ability.can('actions', 'vehicles') && (
                <Link to={`./vehicles/vReparation/${index}`}>
                  <DropdownItem tag='a' href='/' className='w-100'>
                    <Tool size={15} />
                    <span className='align-middle ml-50'>Reparaciones</span>
                  </DropdownItem>
                </Link>
              )}
              {ability.can('delete', 'vehicles') && (
                <Link onClick={(e) => {
                  dispatch(startDeleteRegister(row.id))
                }}>
                  <DropdownItem tag='a' href='/' className='w-100'>
                    <Trash size={15} />
                    <span className='align-middle ml-50'>Eliminar</span>
                  </DropdownItem>
                </Link>
              )}
            </DropdownMenu>
          </UncontrolledDropdown>
        </div>
      )
    }
  }
]